@import url(https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav {
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  height: 65px;
  padding: 0 50px;
  padding-top: 25px;
  margin-bottom: 25px;
  background-color: white;
  width: 100%;
  max-width: 2000px;
}

img {
  width: 100%;
}

.header__left,
.header__right {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 60%;
}

.header__right {
  justify-content: end;
  width: 40%;
}

.header__img--wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid rgba(158, 158, 158, 0.26);
  padding-right: 50px;
}

.header__links {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  list-style-type: none;
  padding-left: 50px;
  width: 100%;
  max-width: 440px;
}

.header__link {
  margin-right: 62px;
}

.header__link:hover .header__link--anchor {
  color: #FF4A52;
}

.header__link--anchor {
  color: black;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  transition: all 300ms ease;
}

.fa-phone {
  color: #FF4A52;
  padding-right: 12px;
}

.header__phone {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__socials {
  display: flex;
  list-style-type: none;
  padding-left: 16px;
  width: 100%;
  justify-content: space-between;
  max-width: 52px;
}

.header__social {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.header__social a {
  color: rgb(105, 105, 105);
  text-decoration: none;
  transition: all 300ms ease;
}

.header__social:hover a {
  color: #FF4A52;
}
.each-slide-effect > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 600px;
  flex-direction: column;
  background-size: cover;
  background-position: center;
}

.each-slide-effect span {
  font-family: "Nothing You Could Do", cursive;
  font-size: 150px;
  text-align: center;
  color: white;
  font-weight: 700;
}

.each-slide-effect p {
  color: white;
  margin-top: 33px;
  padding-bottom: 49px;
  font-size: 20px;
}

.default-nav {
  background-color: rgba(255, 255, 255, 0.2) !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  border-radius: 0 !important;
  height: 120px !important;
  width: 50px !important;
  transition: all 300ms ease;
}

.default-nav svg path {
  fill: white !important;
  transition: all 300ms ease;
}

.default-nav:hover svg path {
  fill: rgb(199, 199, 199) !important;
}

.default-nav:hover {
  background-color: rgba(255, 255, 255, 1) !important;
}

.image__slider button {
  background-color: #1ec6b6;
  border: none;
  color: white;
  font-size: 16px;
  padding: 18px 41px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 300ms ease;
}

.image__slider button:hover {
  background-color: #ff4a52;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}

html {
  scroll-behavior: smooth;
}
.hotels--container {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  flex-direction: column;
  padding-bottom: 40px;
}

.hotels__top--bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.hotels__title {
  font-size: 48px;
  color: #243e63;
}

.hotels__button--container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hotels__button {
  background-color: #1ec6b6;
  border: none;
  border-radius: 50px;
  height: 40px;
  width: 40px;
  margin-left: 18px;
  color: white;
  font-size: 24px;
}
.hotels__wrapper {
  display: flex;
  flex-direction: row;
}

.hotel {
  overflow: hidden;
  height: 561px;
  width: 384px;
  border-top-left-radius: 50px !important;
  border-bottom-right-radius: 50px !important;
}

.hotel__img--wrapper {
  background-size: cover;
  background-position: bottom;
  height: 256px;
}

.hotel__img {
  width: inherit;
  border-top-left-radius: 50px;
}

.hotel__info--container {
  display: flex;
  flex-direction: column;
  padding: 24px 40px;
}

.hotel__info-top--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hotel__title {
  color: #243e63;
  font-size: 22px;
}

.hotel__rating {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.fa-star {
  color: gold;
  height: 24px;
  width: 24px;
}

.hotel__rating--number {
  font-weight: 600;
  color: #243e63;
  font-size: 16px;
  padding-left: 8px;
}

.hotel__info--container {
  display: flex;
  border-left: 1px solid #4a556821;
  border-right: 1px solid #4a556821;
}

.hotel__info-other--container {
  display: flex;
  margin-top: 16px;
}

.hotel__info {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.hotel__info:first-child {
  margin-right: 16px;
}

.fa-location-dot,
.fa-dollar-sign {
  height: 12px;
  width: 12px;
  padding: 8px 9px;
  background-color: #4a5568;
  color: white;
  border-radius: 40px;
  margin-right: 6px;
}

.hotel__info--title {
  font-size: 14px;
  font-weight: 500;
  color: #243e63;
  width: 100%;
}

.hotel__para {
  margin-top: 16px;
  font-size: 14px;
  color: #243e63;
  line-height: 28px;
}

.hotel__info--container2 {
  width: 100%;
}

.hotel--button {
  height: 84px;
  width: 100%;
  border: none;
  text-align: center;
  background-color: #1ec6b6;
  color: white;
  font-size: 18px;
  cursor: pointer;
  font-weight: 500;
  transition: all 300ms ease;
}

.hotel--button:hover {
  background-color: #ff4a52;
}

.carousel {
  display: flex;
  margin: 0 auto;
  align-items: center;
  width: 100%;
  max-width: 1500px;
}
.destinations--wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1400px;
  padding: 0 12px;
  margin: 0 auto;
  margin-top: 200px;
}

.distination__left {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 540px;
}

.destinations__title {
  font-size: 48px;
  color: #243e63;
  text-align: start;
}

.destinations__para {
  color: #7c8ba1;
  font-size: 18px;
  line-height: 32px;
  text-align: start;
  margin-top: 32px;
}

.distination__right {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.destination {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 320px;
  margin: 0 40px;
}

.destination__image {
  height: 320px;
  width: 320px;
  background-position: center;
  background-size: cover;
  border-radius: 4px;
}

.destination__info {
  display: flex;
  flex-direction: column;
  margin-top: 16px;
}

.destination__info-top {
  display: flex;
  justify-content: space-between;
}

.destination__title {
  color: rgb(30, 198, 182);
  font-weight: 600;
  font-size: 18px;
}

.destination__price {
  color: black;
  font-size: 18px;
  font-weight: 500;
}

.destination__about {
  margin-top: 16px;
  text-align: start;
  font-size: 20px;
  font-weight: 600;
}

.destination__tags {
  display: flex;
  justify-content: space-around;
  margin-top: 16px;
}

.destination__tag {
  display: flex;
  align-content: center;
}

.icon {
  color: #718096;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  margin-right: 8px;
}

.tag__title {
  color: #718096;
  font-size: 16px;
  font-weight: 500;
}

.destination__btn {
  color: white;
  font-size: 16px;
  margin-top: 32px;
  border: none;
  background-color: #1ec6b6;
  font-weight: 600;
  padding: 12px 0;
  border-radius: 4px;
  transition: all 300ms ease;
  cursor: pointer;
  text-decoration: none;
}

.destination__btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
footer {
  width: 100%;
  background-color: #2f2f2f;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 120px;
}

.footer__logo--wrapper {
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}

.footer__logo {
  width: 100%;
}

.footer__copyright {
  color: white;
  font-weight: 400;
  font-size: 16px;
  padding-bottom: 40px;
}

.footer__links {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 300px;
  padding: 20px 0;
}

.footer__link {
  color: white;
  font-weight: 500;
  text-decoration: none;
}
